import React from 'react';
import { BrowserRouter as Router, Routes, Route } from 'react-router-dom';
import SignIn from './SignIn';
import SignUp from './SignUp';
import Landing from './Landing'; // 새로 만든 홈 화면 컴포넌트 추가
import Dashboard from './Dashboard'; // 새로 만든 대시보드 컴포넌트 추가

function App() {
  return (
    <Router>
      <Routes>
        <Route path="/" element={<Landing />} /> {/* 초기 홈 화면 */}
        <Route path="/signin" element={<SignIn />} /> {/* 로그인 페이지 */}
        <Route path="/sign-up" element={<SignUp />} /> {/* 회원가입 페이지 */}
        <Route path="/dashboard" element={<Dashboard />} /> {/* 로그인 후 대시보드 페이지 */}
      </Routes>
    </Router>
  );
}

export default App;
